.about-banner-container {
  height: 70%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative; }

.about-banner {
  width: 100%; }

.about-me {
  display: -ms-flexbox;
  display: flex;
  padding-left: 80px;
  padding-right: 80px; }

.profile-about-pic {
  padding: 20px;
  border-radius: 50px;
  height: 300px;
  width: 400px; }

.profile-about {
  -ms-flex: 2 1;
      flex: 2 1;
  padding: 20px; }

