.about-me-timeline {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#FFC522), color-stop(#FF9D22), color-stop(#2892D7), to(#8388EB));
  background-image: -webkit-linear-gradient(#FFC522, #FF9D22, #2892D7, #8388EB);
  background-image: -o-linear-gradient(#FFC522, #FF9D22, #2892D7, #8388EB);
  background-image: linear-gradient(#FFC522, #FF9D22, #2892D7, #8388EB);
  padding-top: 25px; }



